// @flow
import React, { useState, useCallback } from 'react';
import Gallery from 'react-photo-gallery';
import Carousel, { Modal, ModalGateway } from 'react-images';
import desertDoor1 from '../../assets/desert-door/1.jpg';
import desertDoor2 from '../../assets/desert-door/2.jpg';
import desertDoor3 from '../../assets/desert-door/3.jpg';
import desertDoor4 from '../../assets/desert-door/4.jpg';
import desertDoor5 from '../../assets/desert-door/5.jpg';
import north1 from '../../assets/desert-door/north-austin-1.jpg';
import north2 from '../../assets/desert-door/north-austin-2.jpg';
import north3 from '../../assets/desert-door/north-austin-3.jpg';
import north4 from '../../assets/desert-door/north-austin-4.jpg';
import north5 from '../../assets/desert-door/north-austin-5.jpg';
import sorek0 from '../../assets/sorek/Sorek0.jpg';
import sorek1 from '../../assets/sorek/Sorek1.jpg';
import sorek2 from '../../assets/sorek/Sorek2.jpg';
import sorek3 from '../../assets/sorek/Sorek3.jpg';

const Slideshow = () => {
  const [currentImage, setCurrentImage] = useState(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);

  const photos = [
    {
      src: desertDoor1,
      width: 4,
      height: 3
    },
    {
      src: desertDoor2,
      width: 4,
      height: 3
    },
    {
      src: sorek2,
      width: 4,
      height: 3
    },
    {
      src: north1,
      width: 4,
      height: 3
    },
    {
      src: desertDoor5,
      width: 4,
      height: 3
    },
    {
      src: sorek3,
      width: 4,
      height: 3
    },
    {
      src: north4,
      width: 4,
      height: 3
    },
    {
      src: desertDoor4,
      width: 4,
      height: 3
    },
    {
      src: north2,
      width: 4,
      height: 3
    },
    {
      src: sorek0,
      width: 4,
      height: 3
    },
    {
      src: north3,
      width: 4,
      height: 3
    },
    {
      src: sorek1,
      width: 4,
      height: 3
    },
    {
      src: north5,
      width: 4,
      height: 3
    },
    {
      src: desertDoor3,
      width: 4,
      height: 3
    }
  ];

  const openLightbox = useCallback((event, { photo, index }) => {
    setCurrentImage(index);
    setViewerIsOpen(true);
  }, []);

  const closeLightbox = () => {
    setCurrentImage(0);
    setViewerIsOpen(false);
  };

  return (
    <div>
      <Gallery photos={photos} onClick={openLightbox} />
      <ModalGateway>
        {viewerIsOpen ? (
          <Modal onClose={closeLightbox}>
            <Carousel
              currentIndex={currentImage}
              views={photos.map(x => ({
                ...x,
                srcset: x.srcSet,
                caption: x.title
              }))}
            />
          </Modal>
        ) : null}
      </ModalGateway>
    </div>
  );
};

export default Slideshow;
