// @flow
import React, {
  useEffect,
  useState
} from 'react';
import {
  createStyles,
  makeStyles,
  Grid,
  Button,
  IconButton,
  Drawer,
  Hidden
} from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';

import logo from '../../assets/atx_construction.png';
import './index.css';

const useStyles = makeStyles(() => createStyles({
  white: {
    backgroundColor: 'white',
    '& .App-logo': {
      maxHeight: '3rem',
      width: '25%',
      padding: '1rem'
    }
  },
  transparent: {
    opacity: 'transparent',
    padding: '25px 0',
  },
  hidden: {
    visibility: 'hidden'
  },
  visible: {
    visibility: 'visible',
  },
  links: {
    padding: '1rem',
  },
  logoSmall: {
    maxHeight: '50px !important',
    marginLeft: '-1rem'
  }
}));

const Header = () => {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [headerClass, setHeaderClass] = useState(classes.transparent);
  const [display, setDisplay] = useState(true);


  const toggleDrawerOpen = () => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }
    setOpen(true);
  };

  const toggleDrawerClose = () => {
    setOpen(false);
  };

useEffect(() => {
    window.addEventListener('scroll', scrollFunction, true);
  });

  const scrollFunction = () => {
    if (document.body.scrollTop > 200 || document.documentElement.scrollTop > 200) {
      setHeaderClass(classes.white);
      setDisplay(classes.visible);
    } else {
      setHeaderClass(classes.transparent);
      setDisplay(classes.hidden);
    }
  };

    return (
      <div className="header">
        <header className={headerClass}>
          <Grid container direction="row">
            <Hidden smUp>
              <Grid item align="start" xs={1}>
                <IconButton
                  color="black"
                  aria-label="Menu"
                  className="menu"
                  aria-haspopup="true"
                  onClick={toggleDrawerOpen}
                >
                  <MenuIcon />
                </IconButton>
              </Grid>
              <Grid item align="center" xs={11} className={`${display} ? ${classes.hidden} : ''`}>
                <img src={logo} className={classes.logoSmall} alt="logo" />
              </Grid>
            </Hidden>
          </Grid>
          <Hidden xsDown>
            <Grid container direction="row">
              <Grid item align="start" xs={6} className={`${display} ? ${classes.hidden} : ''`}>
                <img src={logo} className="App-logo" alt="logo" />
              </Grid>
              <Grid item align="end" sm={6} className={classes.links}>
                <Button href="#about">
                  About
                </Button>
                <Button href="#portfolio">
                  Portfolio
                </Button>
                <Button href="#services">
                  Services
                </Button>
                <Button href="#contact">
                  Contact
                </Button>
              </Grid>
            </Grid>
          </Hidden>
        </header>
        <Drawer
          open={open}
          anchor="left"
          onClose={toggleDrawerClose}
        >
          <div
            tabIndex={0}
            role="button"
          >
            <div>
              <Button href="#about">
                About
              </Button>
            </div>
            <div>
              <Button href="#portfolio">
                Portfolio
              </Button>
            </div>
            <div>
              <Button href="#services">
                Services
              </Button>
            </div>
            <div>
              <Button href="#contact">
                Contact
              </Button>
            </div>
          </div>
        </Drawer>
        <section>
          <div className="large">
            <img src={logo} className="App-logo" alt="logo" />
          </div>
        </section>
      </div>
    );
};

export default Header;
